body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

@font-face {
  font-family:"Lineage 2 Font";
  src:url(/L2_Panel/static/media/49_la2font.57d0e9f0.eot) format("eot"),
      url(/L2_Panel/static/media/49_la2font.4a8523f7.woff) format("woff"),
      url(/L2_Panel/static/media/49_la2font.4a209cc0.ttf) format("truetype"),
      url(/L2_Panel/static/media/49_la2font.8f07d8d4.svg) format("svg");
      font-weight:normal;
      font-style:normal;
  }

.main {
  margin: auto;
  width: 760px;
  height: 445px;
  margin-top: 60px;
  border-radius: 3%;
  background-size: 100%;
  background-image: url(/L2_Panel/static/media/main-bg.22300542.jpg);
  box-shadow: inset 0px 0px 300px 76px rgba(0,0,0,0.75);
}

.content_wrapper {
  margin-top: 60px;
  color: aliceblue;
}

.progress_wrapper {
  margin-top: 50px;
}




.logo {
    float: right;
    margin-right: 20px;
    margin-top: -70px;
    color: aliceblue;
    font-family: "Lineage 2 Font", sans-serif;
    text-align: center;
}

.logo img {
    height: 70px;
}

.logo img:hover {
    height: 71px;
}

.logo p:hover {
    color: skyblue;
    cursor: pointer;
}


.logo h1 {
    font-size: 78px;
    font-weight: 500;
}

.logo p {
    font-size: 20px;
    margin-top: -5px;
}

@-webkit-keyframes spin { 100% { -webkit-transform: rotate(360deg); } }
@keyframes spin { 100% { -webkit-transform: rotate(360deg); transform:rotate(360deg); } }

.langbar {
    padding-left: 20px;
    display: inline-block;
    color: aliceblue;
    margin-top: 20px;
}

.lang_pill {
    text-align: center;
    display: inline-block;
    border: 1px solid #5B5148;
    border-radius: 40px;
    margin-right: 10px;
    width: 25px;
    font-size: 12px;
}

.lang_pill:hover > img {
    -webkit-animation:spin 0.5s linear;
    animation:spin 0.5s linear;
}

.lang_pill:hover {
    background-color: rgba(75, 32, 42, 0.7);
    cursor: pointer;
}

.lang_pill img {
    margin-top: 5px;
    border-radius: 50%;
    width: 20px;
    height: 20px;
}

.lang_pill p {
    margin: 0 auto;
    padding-bottom: 10px;
}

.active {
    background-color: rgba(75, 32, 42, 0.7);
}
@-webkit-keyframes spin { 100% { -webkit-transform: rotate(360deg); } }
@keyframes spin { 100% { -webkit-transform: rotate(360deg); transform:rotate(360deg); } }

.content {
    margin-left: 485px;
}

.content_button {
    text-align: center;
    border: 1px solid aliceblue;
    border-radius: 10px;
    font-size: 20px;
    margin-top: 0;
    margin-bottom: 10px;
    width: 200px;
    padding-top: 10px;
    padding-bottom: 10px;
    text-decoration: underline;
    box-shadow: 0px 0px 15px 0px rgba(0,0,0,1);
}

.content_button p {
    margin: 0;
    display: inline-block;
    margin-left: 20px;
}

.content_button:hover {
    font-weight: 600;
    border: 1px solid rgb(201, 193, 185);
    cursor: pointer;
}

.sync_icon {
    display: inline-block;
}

.content_button:hover > .sync_icon {
    -webkit-animation:spin 1.5s linear infinite;
    animation:spin 1.5s linear infinite;
    font-weight: 900;
    font-size: 25px;
}

.content_button:hover > .home_icon {
    font-weight: 900;
    font-size: 25px;
}


.progress_bar {
    border-top: 1px solid orange;
    border-bottom: 1px solid orange;
    height: 100px;
    font-size: 15px;
    color: aliceblue;
}

.progress_signs {
    position: absolute;
    display: inline-block;
    width: 152px;
    height: 100px;
    text-align: center;
}

.progress_lines {
    position: absolute;
    display: inline-block;
    width: 380px;
    height: 100px;
    margin-left: 152px;
}

.progress_line {
    display: block;
    width: 90%;
    height: 20px;
    border: 1px solid yellow;
    margin-left: 5%;
    margin-top: 20px;
    border-top-left-radius: 50px;
    border-bottom-right-radius: 50px;
    box-shadow: inset 0px 0px 2px 1px rgba(0,0,0,1);


    
background: rgba(255,182,46,1);
background: -webkit-gradient(left top, right top, color-stop(12%, rgba(255,182,46,1)), color-stop(65%, rgba(255,182,46,1)), color-stop(68%, rgba(255,182,46,1)), color-stop(100%, rgba(253,239,144,1)));
background: -webkit-linear-gradient(left, rgba(255,182,46,1) 12%, rgba(255,182,46,1) 65%, rgba(255,182,46,1) 68%, rgba(253,239,144,1) 100%);
background: linear-gradient(to right, rgba(255,182,46,1) 12%, rgba(255,182,46,1) 65%, rgba(255,182,46,1) 68%, rgba(253,239,144,1) 100%);
filter: progid:DXImageTransform.Microsoft.gradient( startColorstr='#ffb62e', endColorstr='#fdef90', GradientType=1 );
}

.progress_buttons {
    position: absolute;
    display: inline-block;
    width: 137px;
    height: 100px;
    margin-left: 532px;
}

.progress_button {
    width: 127px;
    height: 20px;
    margin-left: 5px;
    margin-top: 20px;
    border-radius: 5px;
    text-align: center;
    border: 1px solid aliceblue;
    text-decoration: underline;
    font-size: 14px;
box-shadow: 0px 0px 15px 0px rgba(0,0,0,1);
}

.progress_button:hover {
    cursor: pointer;
    font-weight: 600;
    border: 1px solid rgb(201, 193, 185)
}

.progress_play {
    text-align: center;
    position: absolute;
    display: inline-block;
    width: 91px;
    margin-left: 669px;
    height: 100px;

}

.progress_play img {
    width: 90%;
    margin-top: -50px;
}

.progress_play img:hover {
    width: 92%;
    cursor: pointer;
}

.progress_play h1 {
    margin-top: -20px;
    cursor: pointer;
}
.foot {
    text-align: center;
}
.foot img {
    margin-top: 5px;
    width: 100px;
    margin-bottom: 0;
    padding-bottom: 0;
}

.foot img:hover {
    cursor: pointer;
}

.foot p {
    font-size: 10px;
    margin-top: -5px;
